
  body {
    background-image: url('../../public/images/arkaplan.jpg'); // Resmin yolu doğru olmalı
    background-size: cover; // Resmi tam ekran yapar
    background-position: center; // Resmi merkeze hizalar
    background-repeat: no-repeat; // Resmin tekrar etmesini engeller
    background-attachment: fixed; // Resmi ekrana sabitler
    margin: 0; // Sayfa kenar boşluklarını sıfırlar
    height: 100vh; // Tam ekran yükseklik
  }


